<template>
  <div class="alarm-statistics">
    <TextTitle title="" />
    <BorderBox1 :w="800" :h="264">
      <div id="line-chart"></div>
    </BorderBox1>
  </div>
</template>

<script>
import { TextTitle, BorderBox1 } from '@/components'
import * as echarts from 'echarts/core';
import {
    LineChart
} from 'echarts/charts';
import {
    TitleComponent,
    TooltipComponent,
    GridComponent,
    LegendComponent
} from 'echarts/components';
import {
    CanvasRenderer
} from 'echarts/renderers';

export default {
  components: {
    TextTitle,
    BorderBox1
  },
  data() {
    return {
      legendList: ['', '', ''],
      lineChart: null,
      ticklineColor: '#5C7E99',
      colors: ['#9E78ED', '#008CFF', '#4DFFF3'] 
    }
  },
  mounted() {
    // 
    echarts.use(
      [LegendComponent, TitleComponent, TooltipComponent, GridComponent, LineChart, CanvasRenderer]
    );
    this.initChart()
  },
  methods: {
    initChart() {
      let ticklineColor = this.ticklineColor
      let legendList = this.legendList
      let color = this.colors
      
      this.lineChart = echarts.init(document.getElementById('line-chart'))

      const option = {
        color,
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: this.legendList,
          right: -5,
          top: -3,
          // orient: 'vertical',
          icon: 'rect',
          textStyle: {
            color: '#A3BACC'
          },
          itemWidth: 8,
          itemHeight: 8,
        },
        grid: {
          top: 66,
          left: 38,
          right: 38,
          bottom: 28,
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['', '', '', '', '', '', ''],
          axisTick: {
            inside: true,
          },
          axisLine: {
            lineStyle: {
              color: ticklineColor
            },
          }
        },
        yAxis: {
          type: 'value',
          axisLine: {
            show: true,
            lineStyle: {
              color: ticklineColor
            }
          },
          splitLine: {
            show: false
          },
          axisTick: {
            show: true,
            inside: true
          }
          
        },
        series: [
          {
            name: legendList[0],
            type: 'line',
            // stack: '',
            smooth: true,
            symbol: 'none',
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: legendList[1],
            type: 'line',
            // stack: '',
            smooth: true,
            symbol: 'none',
            data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
            name: legendList[2],
            type: 'line',
            // stack: '',
            smooth: true,
            symbol: 'none',
            data: [150, 232, 201, 154, 190, 330, 410]
          }
        ]
      }

      this.lineChart.setOption(option)
    }
  }
}
</script>

<style lang="scss">
  .alarm-statistics {
    .border-box-1 {
      margin-top: 12px;
    }

    #line-chart {
      width: 100%;
      height: calc(100% + 32px);
      top: -32px;
    }
  }
</style>